module.exports = "<h2 id=plugins>Plugins</h2> <h3 id=name>name</h3> <p><code>String</code><br>The name of the plugin. This is a required property and should be unique.</p> <h3 id=reuse>reuse</h3> <p><code>Boolean</code><br>Whether the plugin can be used more than once on a class that already uses it, or inherits a class that uses it.</p> <h3 id=silent>silent</h3> <p><code>Boolean</code><br>Whether to display a console warning when the plugin is used multiple times.</p> <h3 id=install>install</h3> <p><code>Function(options)</code> This function is called when the plugin is <code>use</code>d on a Jpex class. It takes an options object with the following properties:</p> <h2 id=install-options>install options</h2> <h3 id=jpex>Jpex</h3> <p><code>Class</code><br>The original Jpex class the plugin was used on.</p> <h3 id=on>on</h3> <p><code>Function(eventName, function)</code> Attaches an event listener to one of the below hooks. The function receives a <code>payload</code> object that contains properties from the event</p> <h2 id=event-hooks>Event Hooks</h2> <h3 id=extend>extend</h3> <p>Called after a new class has been created by extending another. The listener receives the following object:</p> <pre><code class=lang-javascript>{\n  Jpex : &#39;the original class the plugin was registered on&#39;,\n  Class : &#39;the newly-created class&#39;,\n  options : &#39;the options used to create the class&#39;\n}\n</code></pre> <h3 id=options>options</h3> <p>Called when determining the <code>options</code> object before creating a new class. The listener receives the following object:</p> <pre><code class=lang-javascript>{\n  Jpex : &#39;the original class the plugin was registered on&#39;,\n  options : &#39;the options used to create the class&#39;,\n  merge : &#39;a function that merges an object into the current options object i.e. merge({someOption : true})&#39;\n}\n</code></pre> <h3 id=privateproperties>privateProperties</h3> <p>Called when setting private properties on a newly-created class. i.e. the <code>$resolve</code> property and internals like <code>$$factories</code> etc. The listener receives the following object:</p> <pre><code class=lang-javascript>{\n  Jpex : &#39;the original class the plugin was registered on&#39;,\n  Class : &#39;the newly-created class&#39;,\n  options : &#39;the options used to create the class&#39;,\n  apply : &#39;a function that takes an Object containing new properties to attach to the instance. i.e. apply({someProp : &quot;bob&quot;, someGetter : {get : function(){}}})&#39;\n}\n</code></pre> <h3 id=factories>factories</h3> <p>Called when setting factories on a newly-created class. i.e. the <code>register.factory</code> method etc. The listener receives the following object:</p> <pre><code class=lang-javascript>{\n  Jpex : &#39;the original class the plugin was registered on&#39;,\n  Class : &#39;the newly-created class&#39;,\n  options : &#39;the options used to create the class&#39;,\n  register : &#39;a function that takes the name of a factory type, and a function that constructs it, and attaches it to the class\\&#39;s register property&#39;\n}\n</code></pre> <h3 id=beforecreate>beforeCreate</h3> <p>Called when a class is instantiated with the <code>new</code> keyword. The event is fired <em>after</em> dependencies have been resolved, but <em>before</em> the constructor or parent have been invoked. The listener receives the following object:</p> <pre><code class=lang-javascript>{\n  Jpex : &#39;the original class the plugin was registered on&#39;,\n  Class : &#39;the class being instantiated&#39;,\n  options : &#39;the options used to create the class&#39;,\n  instance : &#39;the new instance&#39;,\n  args : &#39;the arguments about to be passed into the constructor function. i.e. an array of resolved dependencies&#39;\n}\n</code></pre> <h3 id=created>created</h3> <p>Called when a class is instantiated with the <code>new</code> keyword. The event is fired <em>after</em> the constructor and parent have been invoked. The listener receives the following object:</p> <pre><code class=lang-javascript>{\n  Jpex : &#39;the original class the plugin was registered on&#39;,\n  Class : &#39;the class being instantiated&#39;,\n  options : &#39;the options used to create the class&#39;,\n  instance : &#39;the new instance&#39;,\n  args : &#39;the arguments about to be passed into the constructor function. i.e. an array of resolved dependencies&#39;\n}\n</code></pre> <h3 id=getfactory>getFactory</h3> <p>Called before Jpex tries to resolve a factory. If the set method is used, it will use this to resolve the factory instead of looking elsewhere.</p> <pre><code class=lang-javascript>{\n  Jpex : &#39;the original class the plugin was registered on&#39;,\n  Class : &#39;the class being instantiated&#39;,\n  factoryName : &#39;the name of the factory being resolved&#39;,\n  set : &#39;a function that accepts any value to be used as the factory. It should be in a valid format, i.e. an object containing either a function, or a value and constant=true properties&#39;\n}\n</code></pre> ";

