module.exports = "<h2 id=jpex-register>Jpex.register</h2> <h3 id=factory>factory</h3> <p><code>Function(name, [dependencies], function)</code></p> <p>See <a href=/guide/factories#factories>factories</a></p> <h3 id=service>service</h3> <p><code>Function(name, [dependencies], function)</code> </p> <p>See <a href=/guide/factories#services>services</a></p> <h3 id=constant>constant</h3> <p><code>Function(name, [dependencies], function)</code> </p> <p>See <a href=/guide/factories#constants>constants</a></p> <h3 id=decorator>decorator</h3> <p><code>Function(name, function)</code> </p> <p>See <a href=/guide/factories#decorators>decorators</a></p> <h2 id=factorywrapper>factoryWrapper</h2> <p><code>Object</code><br>Registering a factory service or constant returns a <em>factoryWrapper</em>. An object that allows additional setup to be configured:</p> <h3 id=lifecycle>lifecycle</h3> <p>Sets the lifecycle of a factory. This determines how long the value of a resolved factory is cached for until it is calculated again. </p> <p><em>note a factory can always be forced to recalculate by calling <code>Jpex.$clearCache()</code></em></p> <pre><code class=lang-javascript>Jpex.register.factory(&#39;myFactory&#39;, function(){}).lifecycle.application();\n</code></pre> <h4 id=application>application</h4> <p>The first time the factory is resolved, its value is cached for the entire application.</p> <h4 id=class>class</h4> <p>Once per class. Every instance of that class will use the same factory instance, but any parent or child classes will create a new instance.</p> <h4 id=instance>instance</h4> <p>Once per instance. Every class instance will recalculate the factory, but its value is passed on to its dependencies.</p> <h4 id=none>none</h4> <p>The factory is calculated every time it is requested, even within the same instance.</p> ";

