module.exports = "<h2 id=dependencies>Dependencies</h2> <h3 id=resolving-dependencies>Resolving Dependencies</h3> <p>There are a number of ways you can request dependencies: </p> <ul> <li>In a new Jpex class&#39;s constructor function: <code>Jpex.extend(function(myService){});</code> </li> <li>In a new Jpex class&#39;s <code>dependencies</code> option: <code>Jpex.extend({dependencies : [&#39;myService&#39;]})</code> </li> <li>Using Jpex&#39;s <code>$resolve</code> method: <code>Jpex.$resolve(&#39;myService&#39;);</code> </li> <li>In a factory or service: <code>Jpex.register.factory(&#39;myService&#39;, [&#39;myOtherService&#39;], function(myOtherService){});</code> </li> </ul> <p>Aside from the <code>$resolve</code> option, all of these methods are lazy: they won&#39;t actually request their dependencies until they have been instantiated themselves.</p> <p>Once a dependency has been requested, Jpex will look in the following locations: </p> <ul> <li>Named Parameters </li> <li>Registered Factories </li> <li><em>Inherited</em> Factories </li> <li>node_modules <em>(in node only)</em></li> </ul> <h3 id=named-parameters>Named Parameters</h3> <p>Named parameters are a way to pass in a specific value when instantiating a class or requesting a factory.</p> <pre><code class=lang-javascript>var Class = Jpex.extend(function (custom) {\n\n});\n\nnew Class({custom : &#39;foo&#39;});\n</code></pre> <p>If you pass in a named parameter that has the same name as an existing factory, Jpex will use the named parameter instead.</p> <pre><code class=lang-javascript>new Class({\n  path : { /*some fake implementation of path*/ }\n});\n</code></pre> <p>This is especially useful when unit testing as you can quickly replace a dependency with a fake implementation.</p> <p>As well as when instantiating classes, you can also pass named parameters into the <code>$resolve</code> method:</p> <pre><code class=lang-javascript>Jpex.register.constant(&#39;myConstant&#39;, &#39;real&#39;);\n\nJpex.register.factory(&#39;myFactory&#39;, function (myConstant) {\n  return &#39;i am &#39; + myConstant;\n});\n\nJpex.$resolve(&#39;myFactory&#39;); // &#39;i am real&#39;\n\nJpex.$resolve(&#39;myFactory&#39;, {myConstant : &#39;fake&#39;}); // &#39;i am fake&#39;\n</code></pre> <h3 id=node-modules>Node Modules</h3> <p>If you request a factory that doesn&#39;t exist, Jpex will attempt to load it from your project&#39;s <code>node_modules</code> folder.</p> <pre><code class=lang-javascript>Jpex.$resolve(&#39;fs&#39;);\n</code></pre> <p>Once Jpex has found a node module, it will register it as a constant for future use.</p> <p><em>This feature only works within node. In a browser or bundler, this feature will not be avaialble.</em></p> <h3 id=optional-dependencies>Optional Dependencies</h3> <p>Dependencies can be made optional by wrapping them in underscores. Normally if a dependency cannot be resolved, an error is thrown. If an optional dependency cannot be found, it will fail silently and return <code>undefined</code>. </p> <pre><code class=lang-javascript>// Although failsToResolve is registered, it depends on a non-existent factory and will throw an error when trying to resolve.\nClass.register.factory(&#39;failsToResolve&#39;, function(doesNotExist){\n  return {};\n});\n\nClass.register.factory(&#39;doesExist&#39;, function(){\n  return {};\n});\n\nClass.$resolve(&#39;_doesNotExist_&#39;); // undefined\nClass.$resolve(&#39;_failsToResolve_&#39;); // undefined\nClass.$resolve(&#39;_doesExist_&#39;); // {}\n</code></pre> <h3 id=object-dependencies>Object Dependencies</h3> <p>One advanced feature is the ability to pass properties into a factory from your class declaration.</p> <p>In your factory you can add a dependency to the special <code>$options</code> key. You can then access the <code>$options</code> object from your factory.</p> <p>This means you can use a single factory for multiple purposes, or accept configuration options.</p> <pre><code class=lang-javascript>var Class = Jpex.extend({\n  dependencies : [{myFactory : &#39;bob&#39;}, {myFactory : &#39;steve&#39;}, &#39;myFactory&#39;],\n  constructor(a, b, c){\n    a.sayHello();\n    b.sayHello();\n    c.sayHello();\n  }\n});\n\nClass.register.factory(&#39;myFactory&#39;, function($options){\n  return {\n    sayHello : function(){\n      return &#39;hello &#39; + ($options || &#39;unknown&#39;);\n    }\n  };\n});\n\nnew Class();\n\n// &#39;hello bob&#39;\n// &#39;hello steve&#39;\n// &#39;hello unknown&#39;\n</code></pre> ";

