module.exports = "<h2 id=extension-inheritance>Extension &amp; Inheritance</h2> <h3 id=extending-jpex>Extending Jpex</h3> <p>Jpex&#39;s <code>extend</code> method returns a brand new class or container that inherits from its parent. Classes can be extended any number of times.</p> <p>Each time you extend a class, the new class inherits its parent&#39;s <code>static</code> properties, prototype <code>methods</code>, registered <code>factories</code>, and configuration options.</p> <hr> <h3 id=invoking-parents>Invoking Parents</h3> <p>A class can be created with or without a constructor function. If you do not provide a constructor function, the parent&#39;s constructor is used:</p> <pre><code class=lang-javascript>var Parent = Jpex.extend(function ($log) {\n  $log(&#39;I am parent constructor&#39;);\n});\n\nvar Child = Parent.extend();\n\nnew Child();\n\n// &#39;I am parent constructor&#39;\n</code></pre> <p>If you provide a constructor function, only the child constructor is used:</p> <pre><code class=lang-javascript>var Parent = Jpex.extend(function ($log) {\n  $log(&#39;I am parent constructor&#39;);\n});\n\nvar Child = Parent.extend(function ($log) {\n  $log(&#39;I am child constructor&#39;);\n});\n\nnew Child();\n\n// &#39;I am child constructor&#39;\n</code></pre> <p>However, you can invoke both the child <em>and</em> parent constructor using the <code>invokeParent</code> option:</p> <pre><code class=lang-javascript>var Parent = Jpex.extend(function ($log) {\n  $log(&#39;I am parent constructor&#39;);\n});\n\nvar Child = Parent.extend({\n  invokeParent : true,\n  constructor($log) {\n    $log(&#39;I am child constructor&#39;);\n  }\n});\n\nnew Child();\n\n// &#39;I am parent constructor&#39;\n// &#39;I am child constructor&#39;\n</code></pre> <hr> <h3 id=class-methods>Class Methods</h3> <p>The methods property of a class allows you to attach methods to the prototype that are available to all instances.</p> <pre><code class=lang-javascript>var Class = Jpex.extend({\n  methods : {\n    myMethod(){\n      ...\n    }\n  }\n});\n\nvar instance = new Class();\ninstance.myMethod();\n</code></pre> <p>Think of methods as manually attaching properties to the <code>function.prototype</code>.</p> <hr> <h3 id=class-properties>Class Properties</h3> <p>Properties offer a way to define properties that will be set on instantiation. There are many ways a property can be set:</p> <pre><code class=lang-javascript>var Class = Jpex.extend({\n  properties : {\n    // Simple properties can have a default primitive value\n    // the value can be overwritten on the instance\n    simpleProperty : &#39;foo&#39;,\n\n\n    // Getters and Setters allow you to transform or validate\n    // a value before committing it\n    transformedProperty : {\n      get(v){\n        return v.toUpperCase();\n      },\n      set(v){\n        if (typeof v !== &#39;string&#39;){\n          throw new Error(&#39;transformedProperty must be a [string]&#39;);\n        }\n        // the returned value will be committed\n        return v.toLowerCase();\n      }\n    },\n\n    // A property can have a watcher that is called\n    // whenever the value is set to a different value\n    // it is not a deep watcher: if you changed instance.watchedProperty.subProperty,\n    // the watcher is not triggered\n    watchedProperty : {\n      watch(value, oldValue){\n        this.dirty = true;\n      }\n    },\n\n    // this is the shorthand for a watched property\n    watchedProperty2(value, oldValue){\n      this.dirty = true;\n    },\n\n    // A property can have a calculated default value, for non-primitives like Objects\n    propertyWithDefaultValue : {\n      default(){\n        return {};\n      }\n    },\n\n    // A property with just a getter can still be written to, set writable to false if you\n    // want a truly read-only property\n    getterProperty : {\n      get(){\n        return &#39;read only value&#39;;\n      },\n      writable : false\n    }\n  }\n});\n</code></pre> <h3 id=static-properties>Static Properties</h3> <p>Static properties are values available on the Class itself. They are inherited by child classes when created, but if a parent&#39;s property is changed, it does not automatically filter down to its children. </p> <pre><code class=lang-javascript>var Class = Jpex.extend({\n  static : {\n    myStaticMethod(){\n      // ...\n    }\n  }\n});\n\nClass.myStaticMethod();\n</code></pre> <h3 id=dependencies>Dependencies</h3> <p>A class&#39;s <a href=/guide/dependencies>dependencies</a> can be declared either in the constructor function, or in the <code>dependencies</code> option:</p> <pre><code class=lang-javascript>\nvar Class = Jpex.extend(function (myService, myFactory) {\n  // ...\n});\n\nvar Class2 = Jpex.extend({\n  dependencies : [&#39;myService&#39;, &#39;myFactory&#39;],\n  constructor(s, f){\n    // ...\n  }\n});\n</code></pre> <p>When you create an instance of the class, the dependencies are resolved.</p> <p>You can attach the dependencies to the class instance using the <code>bindToInstance</code> option. This allows you to access the dependencies outside of the constructor function:</p> <pre><code class=lang-javascript>var Class = Jpex.extend({\n  dependencies : [&#39;myService&#39;],\n  bindToInstance : true,\n  constructor(myService){\n    myService === this.myService; // true\n    this.myService.doSomething();\n  },\n  methods : {\n    myMethod(){\n      this.myService.doSomething();\n    }\n  }\n});\n</code></pre> <p>you can also set <code>bindToInstance</code> to a string and it will attach the dependencies to a property of that name:</p> <pre><code class=lang-javascript>var Class = Jpex.extend({\n  dependencies : [&#39;myService&#39;],\n  bindToInstance : &#39;foo.bah&#39;,\n  constructor(){\n    this.foo.bah.myService.doSomething();\n  }\n});\n</code></pre> ";

