module.exports = "<h2 id=factories>Factories</h2> <h3 id=registering-factories>Registering Factories</h3> <p>Jpex&#39;s Dependency Injection functionality is based around <em>factories</em>. These are small modules that are <em>registered</em> against a Jpex-based class. Whenever that module is requested, Jpex <em>resolves</em> it and returns its value.</p> <p>There are 3 core types of factories that can be registered: Factories, Services, and Constants. There are also Decorators, although these are technically not factories.</p> <hr> <h3 id=factories>Factories</h3> <p>A factory is a function that returns a value and is the basis of all other types of factories. A simple factory takes a <em>name</em> and a <em>function</em>:</p> <pre><code class=lang-javascript>Container.register.factory(&#39;urls&#39;, function () {\n  return {\n    users : &#39;/api/users&#39;\n  };\n});\n</code></pre> <p>When you request the <code>urls</code> factory, it will return <code>{users:&#39;/api/users&#39;}</code>.</p> <p>A factory can also depend on other factories:</p> <pre><code class=lang-javascript>Container.register.factory(&#39;urls&#39;, function (api_url) {\n  return {\n    users : api_url + &#39;/users&#39;\n  };\n});\n</code></pre> <p>When the <code>urls</code> factory is requested, it will first resolve the <code>api_url</code> factory and make it available to the <code>urls</code> factory. </p> <p>If you are writing web-facing code, chances are your code will be minified for production. In this case you will need to provide string literals for your dependencies:</p> <pre><code class=lang-javascript>Container.register.factory(&#39;urls&#39;, [&#39;api_url&#39;], function (api_url) {\n  return {\n    users : api_url + &#39;/users&#39;\n  };\n});\n</code></pre> <p>This will work just like the previous example, except that it doesn&#39;t rely on the argument name to determine the dependency.</p> <h3 id=services>Services</h3> <p>Services are similar to factories except that instead of using the function&#39;s return value, the function is treated like a constructor:</p> <pre><code class=lang-javascript>Container.register.service(&#39;urls&#39;, [&#39;api_url&#39;], function (api_url) {\n  this.users = api_url + &#39;/users&#39;;\n});\n</code></pre> <p>When the <code>urls</code> service is requested, it is instantiated with the <code>new</code> keyword.</p> <h3 id=jpex-as-a-service>Jpex As A Service</h3> <p>Because services are class constructors, it is possible to register another Jpex class as a service:</p> <pre><code class=lang-javascript>var Urls = Jpex.extend({\n  dependencies : &#39;api_url&#39;,\n  constructor(api_url){\n    this.users = api_url + &#39;/users&#39;;\n  }\n});\n\nMainClass.register.service(&#39;urls&#39;, Urls);\n</code></pre> <p>Obviously this example service would be too simple to warrant creating a new Jpex class, but shows how you can create a deep and rich hierarchy of Jpex classes.<br>Also note that you don&#39;t need to declare the string dependencies twice.</p> <h3 id=constants>Constants</h3> <p>Constants are the simplest factories as they just return a static value whenever they are requested:</p> <pre><code class=lang-javascript>Container.register.constant(&#39;urls&#39;, {users : &#39;/api/users&#39;});\n</code></pre> <p>(Note that a constant can&#39;t request any dependencies)</p> <h3 id=decorators>Decorators</h3> <p>Decorators allow you to intercept an existing factory and <em>decorate</em> it before it is passed to the requester.</p> <pre><code class=lang-javascript>Container.register.decorator(&#39;urls&#39;, function (urls) {\n  urls.user += &#39;/test&#39;;\n  return urls;\n});\n</code></pre> <p>The decorator function takes the resolved factory as its only argument and its return value is then returned.</p> <p>If your decorator requires additional dependencies you can use the Class&#39;s <code>$resolve</code> method to request them:</p> <pre><code class=lang-javascript>Container.register.decorator(&#39;urls&#39;, function (urls) {\n  var suffix = this.$resolve(&#39;suffix&#39;);\n  urls.user += &#39;/&#39; + suffix;\n  return urls;\n});\n</code></pre> <p>You can register multiple decorators and they will be chained up accordingly.</p> <hr> <h3 id=lifecycles>Lifecycles</h3> <p>The lifecycle of a factory determines how often a new instance is created.</p> <p>There are four options: <code>application</code>, <code>instance</code>, <code>class</code>, <code>none</code>.<br>The default lifecycle for a factory is <code>instance</code>, meaning it is recalcated for every new instance of a class.</p> <p>You can set the lifecycle of a factory after registering it:</p> <pre><code class=lang-javascript>Container.register.factory(&#39;urls&#39;, function(){}).lifecycle.application();\n</code></pre> <p>You can set the <em>default</em> lifecycle when setting your <em>extenion options</em>:</p> <pre><code class=lang-javascript>var constants = require(&#39;jpex/src/constants&#39;);\nvar Container = Jpex.extend({\n  defaultLifecycle : constants.APPLICATION // 1\n});\n</code></pre> <p>More information on what each lifecycle does can be found in the <a href=/api/factories/lifecycles>api</a>.</p> ";

