module.exports = "<h2 id=getting-started>Getting Started</h2> <h3 id=install>Install</h3> <p>Jpex is available on <a href=https://www.npmjs.com/package/jpex>npm</a>: <code>npm install jpex --save</code></p> <p>You can then include Jpex in your commonJs project:</p> <pre><code class=lang-javascript>var Jpex = require(&#39;jpex&#39;);\nJpex.extend(...);\n</code></pre> <p>Which works out-of-the-box with <em>node</em>, <em>webpack</em> and <em>browserify</em></p> <p>Jpex also comes with a pre-built js file which can be included as a script tag on your page:</p> <pre><code>&lt;script src=&quot;node_modules/jpex/dist/jpex.js&quot;&gt;&lt;/script&gt;\n&lt;script&gt;\nJpex.extend(...);\n&lt;/script&gt;\n</code></pre><p>You can also download the source code from <a href=https://github.com/jpex-js/jpex>github</a></p> <hr> <h3 id=registering-services>Registering Services</h3> <p>Services and factories are small modules or functions that provide a reusable or common piece of functionality. In Jpex, you can register <strong>factories</strong>:</p> <pre><code class=lang-javascript>Jpex.register.factory(&#39;myFactory&#39;, function(){\n  return {};\n});\n</code></pre> <p><strong>services</strong>:</p> <pre><code class=lang-javascript>Jpex.register.service(&#39;myService&#39;, function(){\n  this.method = function(){\n    ...\n  };\n});\n</code></pre> <p>and <strong>constants</strong>:</p> <pre><code class=lang-javascript>Jpex.register.constant(&#39;myConstant&#39;, &#39;foo&#39;);\n</code></pre> <hr> <h3 id=using-factories>Using Factories</h3> <p>Once registered, you can request any factory when creating a new Jpex class:</p> <pre><code class=lang-javascript>var Class = Jpex.extend(function(myFactory, myService, myConstant){\n  myService.method();\n  myConstant === &#39;foo&#39;;\n});\n\nnew Class(); // creates an instance of Class using the above constructor function.\n</code></pre> <p>You can also request a factory from Jpex directly:</p> <pre><code class=lang-javascript>var myService = Jpex.$resolve(&#39;myService&#39;);\nvar myConstant = Jpex.$resolve(&#39;myConstant&#39;);\n\nmyService.method();\nmyConstant === &#39;foo&#39;;\n</code></pre> <p>And finally, you can request a dependency from within another factory:</p> <pre><code class=lang-javascript>Jpex.register.constant(&#39;myConstant&#39;, &#39;foo&#39;);\n\nJpex.register.factory(&#39;myFactory&#39;, function(myConstant){\n  return {\n    injectedValue : myConstant\n  };\n});\n\nJpex.register.service(&#39;myService&#39;, function(myFactory){\n  this.method = function(){\n    return myFactory.injectedValue;\n  };\n});\n\nJpex.$resolve(&#39;myService&#39;).method(); // returns &#39;foo&#39;!\n</code></pre> <hr> <h3 id=extend-amp-instantiate>Extend &amp; Instantiate</h3> <p>You can <code>extend</code> Jpex to create a whole new <em>class</em> with its own constructor and factories. You can also extend an extended class and so on. Each extended class inherits its parent&#39;s properties and factories.</p> <pre><code class=lang-javascript>var Class = Jpex.extend(function(inheritedService){\n  // ...\n});\n\nvar SubClass = Class.extend({\n  constructor : function(inheritedService){\n    ...\n  },\n  invokeParent : true, // will fire off the parent constructor\n  methods : {\n    methodA : function(){\n      // ...\n    }\n  }\n});\n</code></pre> <p>you can invoke a Jpex class with the <code>new</code> keyword. This will create a new instance of the class:</p> <pre><code class=lang-javascript>var instance = new SubClass();\n\ninstance.methodA();\n</code></pre> <hr> <h3 id=plugins>Plugins</h3> <p>There are several <a href=/plugins>plugins</a> available that add extra functionality to Jpex. The most fundamental are <a href=https://www.npmjs.com/package/jpex-node>jpex-node</a> and <a href=https://www.npmjs.com/package/jpex-web>jpex-web</a> that add some useful default factories to your node or web application:</p> <pre><code class=lang-javascript>Jpex.use(require(&#39;jpex-node&#39;));\nJpex.$resolve(&#39;$fs&#39;);\nJpex.$resolve(&#39;$promise&#39;);\n\n// or\nJpex.use(require(&#39;jpex-web&#39;));\nJpex.$resolve(&#39;$window&#39;);\n\n// etc.\n</code></pre> ";

