module.exports = "<h2 id=introduction>Introduction</h2> <h3 id=what-is-jpex>What is Jpex</h3> <p>Jpex is a comprehensive <em>Dependency Injection</em> library that allows you to register services or values against a common container and then inject them at a later point in your application. It also provides an intelligent way to organise your code into inheritable classes for a more Object-Orientated style of application.</p> <h3 id=what-is-dependency-injection>What is Dependency Injection</h3> <p>Dependency Injection is, simply, the process of injecting dependencies into a module. In a normal <em>commonJs</em>-style application, you would use <code>require()</code> to load your dependencies from within your module:</p> <pre><code class=lang-javascript>module.exports = function(){\n  var path = require(&#39;path&#39;);\n  var fs = require(&#39;fs&#39;);\n  var MyService = require(&#39;../services/myService&#39;);\n  var myService = new MyService(&#39;arg1&#39;, &#39;arg2&#39;, &#39;arg3&#39;);\n  // do some stuff\n};\n</code></pre> <p>However, this means that your code is <em>tightly-coupled</em>. In order to use anything from outside your module you need to know <em>where</em> it lives in the file system and exactly <em>how</em> to use it and set it up.</p> <p>If the parameters for the <code>MyService</code> class change in the future, you must find every place in your application that you instantiate it to update the parameter list.</p> <p>It also means <strong>unit testing</strong> becomes very difficult as your dependencies are <em>hard-coded</em> into the module. The only way you can create a mock version of <code>MyService</code> is to use something like <a href=https://www.npmjs.com/package/rewire>Rewire</a> or <a href=https://www.npmjs.com/package/inject-loader>inject-loader</a> to forcibly intercept the <code>require</code> calls.</p> <p>Dependency Injection is a way to abstract this part of the application into a separate process. Your calling code shouldn&#39;t care what dependencies another module uses. But similarly, your module shouldn&#39;t care where its dependencies come from, or how they&#39;re constructed.</p> <h3 id=dependency-injection-with-jpex>Dependency Injection with Jpex</h3> <p>Using Jpex, dependencies are registered with instructions on how to construct them. You can then request dependencies without having to worry about where they come from:</p> <pre><code class=lang-javascript>module.exports = Jpex.extend(function(path, fs, myService){\n  // do some stuff\n});\n</code></pre> <p>This function now has access to <code>path</code>, <code>fs</code>, and a pre-constructed <code>myService</code> instance without having to do any more work.</p> <p>At some earlier part of your application, you can register <code>myService</code>:</p> <pre><code class=lang-javascript>Jpex.register.service(&#39;myService&#39;, function(arg1, arg2, arg3){\n  // ...\n});\n</code></pre> <p><code>myService</code> is now available throughout the application and the construction of the service is handled by Jpex.</p> <hr> <h3 id=javascript-classes>Javascript Classes</h3> <p>Classes in javascript have always been awkward due to its prototypical nature. A class is essentially just a function that can be constructed:</p> <pre><code class=lang-javascript>function MyClass(){\n  this.method = function(){};\n}\nvar instance = new MyClass();\n</code></pre> <p>other than sticking to a strict naming convention, it&#39;s not clear what is a function and what is a class as they both use the same syntax. This leads to other confusing bugs:</p> <pre><code class=lang-javascript>MyClass();\n// calling MyClass without new has attached method to the global context\nwindow.method();\n</code></pre> <p>Inheritance is also an awkward construct to implement in javascript:</p> <pre><code class=lang-javascript>function InheritedClass(){\n\n}\n// Create a new prototype object that uses the parent class&#39;s prototype as its prototype!\nInheritedClass.prototype = Object.create(MyClass.prototype);\nInheritedClass.prototype.constructor = InheritedClass;\n// We may want to inherit static properties?\nObject.keys(MyClass).forEach(key =&gt; InheritedClass[key] = MyClass[key]);\n</code></pre> <h3 id=jpex-classes>Jpex Classes</h3> <p>Jpex&#39;s class system allows you to easily create a new class:</p> <pre><code class=lang-javascript>var Class = Jpex.extend(function(){\n  /// constructor function\n});\n</code></pre> <p>inherit another class:</p> <pre><code class=lang-javascript>var SubClass = Class.extend();\n</code></pre> <p>and configure all sorts of additional options:</p> <pre><code class=lang-javascript>var SubSubClass = SubClass.extend({\n  dependencies : [&#39;myFactory&#39;, &#39;$log&#39;],\n  bindToInstance : true,\n  constructor : function(){},\n  invokeParent : true,\n  methods : {\n    method : function(){}\n  },\n  properties : {\n    property : 1234\n  },\n  static : {\n    staticValue : &#39;foo&#39;\n  },\n  defaultLifecycle : 1\n});\n</code></pre> <p>and of course, Jpex classes can have dependency injection and unordered parameters:</p> <pre><code class=lang-javascript>var Class = Jpex.extend(function (myService, width, height) {\n\n});\n\nvar instance = new Class({width : 100, height : 150}); // myService is injected automatically\n</code></pre> <hr> <h3 id=history>History</h3> <p>Jpex (or <em>Javascript Prototype Extension</em>) was originally written as a simple inheritance wrapper to quickly create new and extendable classes.</p> <p>It soon became something much more comprehensive, introducing inheritable static properties and configurable prototypes.</p> <p>Eventually dependency injection was added and the usefulness of this feature quickly became Jpex&#39;s main focus.</p> ";

