module.exports = "<h2 id=plugins>Plugins</h2> <h3 id=using-plugins>Using Plugins</h3> <p>There are a number of plugins available. You can see some of them <a href=/plugins>here</a>. Jpex plugins are very open-ended and allow for a lot of extra features to be added.</p> <p>Using a plugin is simple - just <code>require</code> it and <code>use</code> it:</p> <pre><code class=lang-javascript>var Jpex = require(&#39;jpex&#39;);\nvar plugin = require(&#39;jpex-folder&#39;);\n\nJpex.use(plugin, { /* plugin options */ });\n</code></pre> <p>and that&#39;s it. You can now use the plugin&#39;s functionality in your application:</p> <pre><code class=lang-javascript>Jpex.register.folder(&#39;./path/to/folder&#39;);\n</code></pre> <p>It&#39;s also worth noting that you don&#39;t <em>have</em> to use a plugin on the original Jpex object. You can apply a plugin to only a specific class:</p> <pre><code class=lang-javascript>var Class = Jpex.extend();\n\nvar SubClass = Class.extend();\n\nSubClass.use(plugin);\n\nJpex.register.folder === undefined;\nClass.register.folder === undefined;\nSubClass.register.folder !== undefined;\n</code></pre> <h3 id=writing-plugins>Writing Plugins</h3> <p>A plugin can be extremely simple or highly complex. The basic format for a plugin is as follows:</p> <pre><code class=lang-javascript>{\n  name : &#39;plugin-name&#39;,\n  install : function(config){\n    // Add something to the Jpex class the plugin was used on\n    config.Jpex.someStaticProperty = &#39;plugged in!&#39;;\n\n    // Inspect the plugin&#39;s configuration options:\n    if (config.options.skipCertainSteps){\n      return;\n    }\n\n    // Attach event listeners\n    config.on(&#39;extend&#39;, function(evt){\n\n    });\n  }\n}\n</code></pre> <p>A plugin <em>must</em> have a unique <code>name</code> property, and it <em>must</em> have an <code>install</code> method.</p> <h3 id=install>Install</h3> <p>The install method is where you add event listeners and handle one-time configurations. The single argument contains the Class the plugin was used on, any configuration options passed into the <code>use</code> method, and an <code>on</code> method that creates an event handler.</p> <h3 id=hooks-events>Hooks &amp; Events</h3> <p>Event hooks are the most important part of Jpex plugins. They allow you to intercept Jpex&#39;s internal library and enhance or even replace its functionality.</p> <p>A standard hook looks something like this:</p> <pre><code class=lang-javascript>on(&#39;created&#39;, function (evt) {\n  // Add something to the Jpex class the plugin was used on\n  evt.Jpex.something = true;\n\n  // Add something to the current Class\n  evt.Class.instantiated = true;\n\n  // Each event will have some specific properties\n  var instance = evt.instance;\n  instance.args = evt.args;\n});\n</code></pre> <p>Some hooks will also provide some helper methods:</p> <pre><code class=lang-javascript>on(&#39;getFactory&#39;, function (evt) {\n  if (evt.factoryName === &#39;foobah&#39;){\n    var value = {\n      fn : function () {\n        return &#39;intercepted factory&#39;;\n      }\n    };\n\n    evt.set(value);\n  }\n});\n</code></pre> <p>Details on each hook can be found in the <a href=/api>api</a>.</p> <h3 id=jpex-lifecycle>Jpex Lifecycle</h3> <p>When <code>Jpex.extend</code> is called the following process occurs, including the firing of various events:</p> <p><img src=" + __webpack_require__(27) + " alt=\"Extend Lifecycle\"></p> <p>When <code>new Class()</code> is called, another process occurs. This is slightly more complex because it includes resolving nested dependencies and recursive invocation:</p> <p><img src=" + __webpack_require__(29) + " alt=\"Instance Creation Lifecycle\"></p> <h3 id=triggering-custom-events>Triggering custom events</h3> <p>Internally, events are triggered using the <code>$trigger</code> class method. It is therefore possible to trigger custom events from within your plugin:</p> <pre><code class=lang-javascript>on(&#39;extend&#39;, function (evt) {\n  if (evt.Class.somePropertyCheck){\n    var payload = {\n      Class : evt.Class,\n      customProperty : &#39;x&#39;\n    };\n    evt.Class.$trigger(&#39;myCustomEvent&#39;, payload);\n  }\n});\n</code></pre> <p>You can then listen to these custom events like any other:</p> <pre><code class=lang-javascript>on(&#39;myCustomEvent&#39;, function (evt) {\n\n});\n</code></pre> <hr> <h3 id=best-practices>Best Practices</h3> <h4 id=use-es5-safe-syntax>Use ES5-safe syntax</h4> <p>It&#39;s not always clear in what context a plugin will be used so don&#39;t assume the environment will have ES6 features, or use an ES6-capable transpiler.</p> <h4 id=check-for-dependent-plugins>Check for dependent plugins</h4> <p>If you write a plugin that needs another plugin to be installed, you should check if it has been used:</p> <pre><code class=lang-javascript>if (!Jpex.$$using[&#39;some-other-plugin&#39;]){\n  // ...\n}\n</code></pre> <p>At this point you can either <code>require</code> and <code>use</code> the dependent plugin, or throw an error.</p> <h4 id=auto-register-safe-plugins>Auto-register safe plugins</h4> <p>If a plugin is not going to affect existing functionality, like registering some default factories, then you may want to automatically install it when included via a <code>&lt;script&gt;</code> tag:</p> <pre><code class=lang-javascript>if (typeof window !== &#39;undefined&#39; &amp;&amp; window.Jpex &amp;&amp; window.Jpex.use){\n  window.Jpex.use(module.exports);\n}\n</code></pre> <h4 id=provide-a-dist-version>Provide a dist version</h4> <p>If writing a browser-capable plugin, it is a good idea to provide a precompiled version that can be included with a <code>&lt;script&gt;</code> tag or without using <em>webpack</em> or <em>browserify</em>.<br>There are several utilities out there to do this. Internally, Jpex is built using <a href=https://github.com/jpex-js/jpex-build-tools>jpex-build-tools</a> which is an extremely basic bundler.</p> <h4 id=test>Test</h4> <p>If you plan on publishing and distributing a plugin, please provide some meaningful unit tests to prove it works!</p> ";

