module.exports = "<h2 id=jpex-defaults>jpex-defaults</h2> <p>Core factories for Jpex</p> <p>This package contains a number of injectable factories for <em>Jpex</em>. </p> <h3 id=installation>Installation</h3> <p>Install via npm </p> <pre><code>npm install jpex-defaults --save\n</code></pre><h3 id=usage>Usage</h3> <p>Node: </p> <pre><code class=lang-javascript>const Jpex = require(&#39;jpex&#39;);\nconst defaults = require(&#39;jpex-defaults&#39;);\nJpex.use(defaults);\n\n// Default factories are now available\nconst t = Jpex.$resolve(&#39;$timeout&#39;);\n</code></pre> <p>Webpack/Browserify: </p> <pre><code class=lang-javascript>var Jpex = require(&#39;jpex&#39;);\nvar defaults = require(&#39;jpex-defaults&#39;);\nJpex.use(defaults);\n</code></pre> <p>or...</p> <pre><code class=lang-javascript>var Jpex = require(&#39;jpex&#39;);\nvar defaults = require(&#39;jpex-defaults/dist/jpex-defaults&#39;);\nJpex.use(defaults);\n</code></pre> <p>HTML/Javascript</p> <pre><code class=lang-html>&lt;script src=&quot;node_modules/jpex/dist/jpex.js&quot;&gt;&lt;/script&gt;\n&lt;script src=&quot;node_modules/jpex-defaults/dist/jpex-defaults.js&quot;&gt;&lt;/script&gt;\n&lt;script&gt;\n// jpex-defaults is automatically registered when imported via script tags\nvar t = Jpex.$resolve(&#39;$timeout&#39;);\n&lt;/script&gt;\n</code></pre> <h2 id=api>API</h2> <h3 id=-timeout>$timeout</h3> <p><code>$timeout(callback, delay)</code><br><code>$timeout</code> is the equivalent of the <code>setTimeout</code> function. The callback function is called after the value of <em>delay</em> has passed. The callback function is only called once. </p> <h3 id=-interval>$interval</h3> <p><code>$interval(callback, delay)</code><br><code>$interval</code> is the equivalent of the <code>setInterval</code> function. <code>$interval</code> will be called repeatedly at the interval set by <em>delay</em>. </p> <h3 id=-immediate>$immediate</h3> <p><code>$immediate(callback)</code> <code>$immediate</code> calls the callback function on the next available event loop. This is the equivalent of <code>setImmediate</code> in <em>NodeJs</em> or <code>setTimeout(fn, 0)</code> in a browser. </p> <h3 id=-timeout-clear>$timeout.clear</h3> <p><code>$timeout.clear(timer)</code><br><code>$timeout</code>, <code>$interval</code>, and <code>$immediate</code> all have a <code>clear</code> method which is the equivalent of calling <code>clearTimeout(timer)</code>.</p> <h3 id=-log>$log</h3> <p><code>$log(message, [message2, message3...])</code><br>This is a wrapper for the console functions</p> <pre><code class=lang-javascript>$log(&#39;I am console.log&#39;);\n</code></pre> <h4 id=-log-log>$log.log</h4> <pre><code class=lang-javascript>$log.log(&#39;I am also console.log&#39;);\n</code></pre> <h4 id=-log-warn>$log.warn</h4> <pre><code class=lang-javascript>$log.warn(&#39;I am console.warn&#39;);\n</code></pre> <h4 id=-log-error>$log.error</h4> <pre><code class=lang-javascript>$log.error(&#39;I am console.error&#39;);\n</code></pre> <h3 id=-promise>$promise</h3> <p><code>$promise(callback)</code><br>This wraps up the native <code>Promise</code> class, without the need for the <code>new</code> keyword. If using $promise in a browser that doesn&#39;t support the <em>Promise</em> object, a polyfill must be attached to the <em>window</em> object. The plugin <a href=/plugins/jpex-web><em>jpex-web</em></a> (which also includes this package) automatically sets a polyfill if needed. </p> <p>Calling $promise will create a new Promise with the provided constructor function.<br>The function takes two parameters: resolve and reject.</p> <pre><code class=lang-javascript>return $promise(function(resolve, reject){\n  resolve(123);\n});\n</code></pre> <h4 id=-promise-resolve>$promise.resolve</h4> <p>Returns a resolved promise.</p> <pre><code class=lang-javascript>return $promise.resolve(123);\n</code></pre> <h4 id=-promise-reject>$promise.reject</h4> <p>Returns a rejected promise.</p> <pre><code class=lang-javascript>return $promise.reject().catch(...);\n</code></pre> <h4 id=-promise-all>$promise.all</h4> <p>Accepts an array of promises and resolves once all promises have been resolved. If any of the promises is rejected, <code>$promise.all</code> will be rejected.</p> <pre><code class=lang-javascript>$promise.all([$promise(...), $promise.resolve(), 123]);\n</code></pre> <h4 id=-promise-race>$promise.race</h4> <p>Accepts an array of promises and resolves when any of the promises has been resolved. If any of the promises is rejected, <code>$promise.all</code> will be rejected.</p> <pre><code class=lang-javascript>$promise.race([$promise(...), $promise.resolve(), 123]);\n</code></pre> <h3 id=-typeof>$typeof</h3> <p><code>$typeof(any)</code><br><code>$typeof</code> provides a function that returns the type of any object. The possible return values are: </p> <ul> <li>string </li> <li>number </li> <li>boolean </li> <li>function </li> <li>array </li> <li>object </li> <li>date </li> <li>regexp </li> <li>null </li> <li>undefined</li> </ul> <pre><code class=lang-javascript>var t = $typeof(/a/); // &#39;regexp&#39;\n</code></pre> <h3 id=-copy>$copy</h3> <p><code>$copy(object)</code><br>The $copy factory allows you to create a copy any object. It can be used as a function, or as an object which contains shallow and deep copy functions.<br>The shallow copy will copy an object but will not create copies of its properties. So if an object has a property <code>x</code> which is an object itself, the copied object will have the exact same instance of x. </p> <pre><code class=lang-javascript>var obj = { x : [1, 2, 3] };\nvar shallow = $copy(obj);\n</code></pre> <h4 id=-copy-shallow>$copy.shallow</h4> <p><code>$copy.shallow(object)</code> </p> <pre><code class=lang-javascript>var obj = { x : [1, 2, 3] };\nvar shallow = $copy.shallow(obj);\n</code></pre> <h4 id=-copy-deep>$copy.deep</h4> <p><code>$copy.deep(object)</code><br>The deep copy will copy an object and then copy all of that object&#39;s properties. </p> <pre><code class=lang-javascript>var obj = { x : [1, 2, 3] };\nvar deep = $copy.deep(obj);\n</code></pre> <h4 id=-copy-extend>$copy.extend</h4> <p><code>$copy.extend(target, [obj1, obj2...])</code><br>The extend method takes any number of arguments and will deep copy the properties of the last arguments onto the first argument. i.e. <code>$copy.extend(a, b, c)</code> will copy the properties of <code>b</code> onto <code>a</code> and then <code>c</code> onto <code>a</code>. If <code>b</code> and <code>c</code> have the same property, <code>c</code>&#39;s property wins.<br>There are a couple of rules to keep in mind:<br>If two objects have the same property, and the type of that property is an <em>object</em>, its properties will be combined. i.e. if <code>b.x.foo</code> is set and <code>c.x.bah</code> is set, then <code>a.x</code> will get both properties <code>foo</code> and <code>bah</code>.<br>If two objects have the same property, and the type of that property is an <em>array</em>, its elements will be overwritten. i.e. if <code>b.x = [1, 2, 3]</code> and <code>c.x = [4, 5]</code> then <code>a.x</code> will be <code>[4,5]</code>. </p> <pre><code class=lang-javascript>var obj = { x : [1, 2, 3] };\nvar extended = $copy.extend(0, 1); // 1\n\nextended = $copy.extend({}, obj); // { x : [1,2,3] }\n\nextended = $copy.extend({}, obj, {y : &#39;why&#39;}); // { x : [1,2,3], y : &#39;why&#39; }\n\nextended = $copy.extend({}, {x : &#39;first&#39;}, {x : &#39;second&#39;}); // { x : &#39;second&#39; }\n\nextended = $copy.extend({}, {x : [1, 2, 3]}, {x : [4, 5, 6]}); // { x : [1, 2, 3, 4, 5, 6]}\n</code></pre> ";

