module.exports = "<h2 id=jpex-folders-plugin>Jpex Folders Plugin</h2> <p>Automatically register factories/services from files in folders.</p> <h3 id=installaction>Installaction</h3> <p><code>npm install jpex-folder</code></p> <h3 id=usage>Usage</h3> <pre><code class=lang-javascript>var Jpex = require(&#39;jpex&#39;);\nvar plugin = require(&#39;jpex-folder&#39;);\nJpex.use(plugin);\n\n// Register all factories in current_working_directory/factories\nJpex.register.folder(&#39;factories&#39;, options);\n</code></pre> <p>This means you can organise your application in a logical manner and then have all dependencies automatically loaded and injected into your jpex classes. </p> <h2 id=options>Options</h2> <p>The Options parameter takes the following options: </p> <h3 id=type>type</h3> <p>The type of dependency you want to register them as. If not specified it will register all functions as factories and anything else as a constant. Valid values are <em>factory, service, constant, &#39;auto&#39;</em></p> <h3 id=lifecycle>lifecycle</h3> <p>The default lifecycle to register a factory as. This can either be a string or a number.</p> <pre><code class=lang-javascript>Jpex.register.folder(&#39;factories&#39;, { lifecycle : &#39;application&#39; });\nJpex.register.folder(&#39;services&#39;, { lifecycle : 1 });\nvar constants = require(&#39;jpex/src/constants&#39;);\nJpex.register.folder(&#39;other&#39;, { lifecycle : constants.APPLICATION });\n</code></pre> <p>If not specified it will default to <em>&#39;instance&#39;</em>.<br>It is also possible to set a specific lifecycle within the file being loaded in...</p> <pre><code class=lang-javascript>// factories/someFactory.js\nmodule.exports = function(){\n  // do some factory stuff\n};\nmodule.exports.lifecycle = &#39;application&#39;;\n</code></pre> <p>this will override the default lifecycle for that file.</p> <h3 id=dependencies>dependencies</h3> <p>Although not an option it is worth noting here that you can also specify dependencies on the individual files...</p> <pre><code class=lang-javascript>// factories/someFactory.js\nmodule.exports = function () {\n  // ...\n};\nmodule.exports.dependencies [&#39;foo&#39;, &#39;bah&#39;];\n</code></pre> <h3 id=prefix>prefix</h3> <p>A prefix to add to the start of each dependency </p> <h3 id=suffix>suffix</h3> <p>A suffix to add to the end of each dependency </p> <p>###prefixFolder<br>When adding sub folders, this will prepend the folder name to the dependency, so <em>main</em> folder with a <em>sub.js</em> file will be registered as <em>mainSub</em>, for example.</p> <p>###pattern<br>A glob pattern to match by. This defaults to <code>&#39;**/*.js&#39;</code></p> <p>###transform<br><code>function(base, folders, extension, options){}</code><br>A function that will transform the filename into the name of the dependency. This is entirely optional and will be handled automatically using the above option values if omitted.<br>The function takes 4 parameters: the file name (without the folders or file extension), an array of folders leading to the file, the file extension (.js). and the main options object.<br>The <code>this</code> object will be the Class the folders are being registered against.<br>The value returned by this function will be used as the dependency name. </p> <p>###register<br><code>function(filename, content, options){}</code> Another optional function that takes the name of the dependency and the contents of the related file and registers the dependency against the class. </p> ";

